	module.exports = {"data":{"site":{"siteMetadata":{"title":"DeepCold Blog","author":"DeepCold"}},"markdownRemark":{"id":"/Users/shenhan/iserver/deepcoldy/src/pages/older/2017-10-23.md absPath of file >>> MarkdownRemark","html":"<p>加入了我个人认为最重要的功能——不确定环境的服务端渲染，以下为介绍。</p>\n<blockquote>\n<h2>Environment Agnostic SSR</h2>\n<p>The default build of <code class=\"language-text\">vue-server-renderer</code> assumes a Node.js environment, which makes it unusable in alternative JavaScript environments such as php-v8js or Nashorn. In 2.5 we have shipped a build that is largely environment-agnostic, which makes it usable in the environments mentioned above.</p>\n<p>For both environments, it is necessary to first prepare the environment by mocking the <code class=\"language-text\">global</code> and <code class=\"language-text\">process</code> objects, with <code class=\"language-text\">process.env.VUE_ENV</code> set to <code class=\"language-text\">&quot;server&quot;</code>, and <code class=\"language-text\">process.env.NODE_ENV</code> set to <code class=\"language-text\">&quot;development&quot;</code> or <code class=\"language-text\">&quot;production&quot;</code>.</p>\n<p>In Nashorn, it may also be necessary to provide a polyfill for <code class=\"language-text\">Promise</code> or <code class=\"language-text\">setTimeout</code> using Java’s native timers.</p>\n<p>Example usage in php-v8js:</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">&lt;?php\n  $vue_source = file_get_contents(&#39;/path/to/vue.js&#39;);\n  $renderer_source = file_get_contents(&#39;/path/to/vue-server-renderer/basic.js&#39;);\n  $app_source = file_get_contents(&#39;/path/to/app.js&#39;);\n\n  $v8 = new V8Js();\n\n  $v8-&gt;executeString(&#39;var process = { env: { VUE_ENV: &quot;server&quot;, NODE_ENV: &quot;production&quot; }}; this.global = { process: process };&#39;);\n  $v8-&gt;executeString($vue_source);\n  $v8-&gt;executeString($renderer_source);\n  $v8-&gt;executeString($app_source);\n?&gt;</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// app.js</span>\n<span class=\"token keyword\">var</span> vm <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Vue</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  template<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`&lt;div>{{ msg }}&lt;/div>`</span></span><span class=\"token punctuation\">,</span>\n  data<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    msg<span class=\"token punctuation\">:</span> <span class=\"token string\">'hello'</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// exposed by vue-server-renderer/basic.js</span>\n<span class=\"token function\">renderVueComponentToString</span><span class=\"token punctuation\">(</span>vm<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">(</span>err<span class=\"token punctuation\">,</span> res<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">print</span><span class=\"token punctuation\">(</span>res<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<h2>还有TypeScript 支持</h2>\n<p><a href=\"https://medium.com/the-vue-point/upcoming-typescript-changes-in-vue-2-5-e9bd7e2ecf08\">TypeScript Changes</a></p>","frontmatter":{"title":"Vue2.5发布","date":"October 23, 2017"}}},"pathContext":{"slug":"/older/2017-10-23/","previous":{"fields":{"slug":"/older/2017-09-09/"},"frontmatter":{"title":"思考一个很有意思的问题——换肤"}},"next":{"fields":{"slug":"/react/2017-12-12/"},"frontmatter":{"title":"RN入门教程，个人整理版本"}}}}

